@import "../../global.scss";

.projects {
  background-color: $mainLightColor;
  // height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  .title {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    @include mobile {
      align-items: center;
    }

    h1 {
      font-family: "Montserrat";
      font-weight: lighter;
      font-size: 60px;
      margin: 10px 0;
      color: black;
      margin-right: 15px;
      @include mobile {
        font-size: 40px;
      }
    }
    h2 {
      font-family: "Montserrat";
      font-weight: lighter;
      font-size: 60px;
      margin: 10px 0;
      color: $mainColor;
      @include mobile {
        font-size: 40px;
      }
    }
  }
  p {
    font-family: "Manrope";
    font-size: $mainSize;
    margin-bottom: 20px;
    @include mobile {
      // margin: $mainSize;
      margin-left: $mainSize;
      margin-right: $mainSize;
    }
  }
  
  .card-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-bottom: 65px;

    .proj {
      background-color: #b9ae9c;
      max-height: 300px;
      max-width: 420px;
      height: 20vw;
      width: 28vw;
      box-shadow: 1px 1px 4px $mainColor;
      margin: 0.5vw;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      white-space: normal;
      object-fit: cover;
      transition: all 0.5s ease;
      position: relative;

      @include mobile {
        width: 80vw;
        height: 50vw;
        margin: 5px;
      }

      &:hover {
        transform: scale(1.05);
        box-shadow: 1px 1px 4px $secondaryColor;
      }

      img {
        display: block;
        height: 100%;
        width: 100%;
        object-fit: cover;
        position: relative;
      }
      .vertical {
        display: block;
        height: 100%;
        width: 100%;
        object-fit: scale-down;
        position: relative;
      }
      .zoom {
        display: block;
        height: 100%;
        width: 100%;
        object-fit: cover;
        position: relative;
      }

      .blur {
        backdrop-filter: blur(3px);
      }

      .overlay {
        width: 100%;
        height: 100%;
        display: flex;
        top: 0;
        left: 0;
        flex-direction: column;
        position: absolute;
        align-items: center;
        justify-content: center;
        background: rgba(127, 84, 99, 0.8);
        opacity: 0;
        transition: opacity 0.25s;

        > * {
          transform: translateY(20px);
          transition: transform 0.25s;
        }
        &:hover {
          opacity: 1;
          > * {
            transform: translateY(0);
            color: white;
          }
        }

        h3 {
          font-size: $mainSize;
          font-family: "Montserrat";
          font-weight: bold;

          @include mobile {
          }
        }
        h4 {
          font-family: "Manrope";
          // font-size: 18px;
          font-weight: normal;
          text-align: center;
        }
        p {
          font-size: 18px;
          margin-top: $mainSize;
          margin-left: $mainSize;
          margin-right: $mainSize;
          margin-bottom: 0;
          font-family: "Montserrat";

          @include mobile {
            font-size: 15px;
            margin: 3px;
          }
        }
        .btn-container {
          display: flex;
          flex-direction: row;
          margin-bottom: $mainSize;
          @include mobile {
            margin-bottom: 0;
          }

          a {
            text-decoration: none;
            display: flex;
            margin: 10px;
            @include mobile {
              margin: 5px;
            }
            i {
              align-self: center;
              justify-content: center;
              color: white;
              object-fit: cover;
              font-size: 30px;

              @include mobile {
                font-size: 20px;
              }

              &:hover {
                color: #ccb3bb;
              }
            }
          }
        }
      }
    }

